@import "../components/variables";
@import "../components/mixin";

body{
  .header{
    .container{
      .header__logo{
        span{
          display: none;
        }
      }
    }
  }
  .about__us{
    width: 100%;
    padding-top: 25px;
    .containerParent();
    .container{
      .containerChild();
      margin-bottom: 70px;
      display: grid;
      grid-template-areas: "breadcrumb"
                           "aboutUs";
      grid-template-columns: 100%;
      .content__aboutUs__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__aboutUs{
        grid-area: aboutUs;
        &__headline{
          display: flex;
          justify-content: space-between;
          align-items: center;
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          a{
            display: block;
            width: 250px;
            height: 50px;
            font: 1em/50px @sansbold;
            color: #fff;
            text-align: center;
            text-transform: uppercase;
            text-decoration: none;
            background: @mainColor;
            border-radius: 5px;
            &:hover{
              background-color: @hoverColor;
              transition: .5s;
            }
          }
        }
        h6 {
          margin: 5px 0;
          font: 1.25em/1.375em @sansbold;
          color: @linkColor;
        }
        p{
          margin: 20px 0;
          font: 1.125em/1.25em @sansregular;
          color: @linkColor;
        }
      }
    }
  }
}